import { useNavigate } from 'react-router-dom'
import { Title, LabelInput } from '../../components/index'
import * as S from './style'
import React, { useState } from 'react'
import { useEffect } from 'react'

function SignUp() {
  const [email, setEmail] = useState('')
  const [pw, setPw] = useState('')
  const [pwSame, setPwSame] = useState('')
  const [name, setName] = useState('')

  const [emailValid, setEmailValid] = useState(false)
  const [nameValid, setNameValid] = useState(false)
  const [pwValid, setPwValid] = useState(false)
  const [pwSameValid, setPwSameValid] = useState(false)

  const [notAllow, setNotAllow] = useState(true)

  const navigate = useNavigate()

  const handleEmail = e => {
    setEmail(e.target.value)
    const regex =
      /^(([^<>()[\].,;:\s@"]+(\.[^<>()[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i
    if (regex.test(email)) {
      setEmailValid(true)
    } else {
      setEmailValid(false)
    }
  }

  const handlePassword = e => {
    const value = e.target.value
    setPw(value)
    const regex =
      /^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{8,20}$/
    const isValid = value.length >= 8 && regex.test(value)
    setPwValid(isValid)
  }

  const handlePasswordSame = e => {
    const value = e.target.value
    setPwSame(value)
    const isValid = value === pw
    setPwSameValid(isValid)
  }
  const handleName = e => {
    const value = e.target.value
    setName(value)
    const isValid = value.length >= 2
    setNameValid(isValid)
  }

  const onClickConfirmButton = () => {
    //      
    if (emailValid && pwValid && pwSameValid && nameValid) {
      //       API 
      // fetch  axios   API  
      const currentDate = new Date()
      const isoDate = currentDate.toISOString()
      const data = {
        email: email,
        name: name,
        password: pw,
        registDate: isoDate,
      }

      const confirm = window.confirm(' ?')
      if (confirm) {
        fetch('/api/auth/signup', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify(data),
        })
          .then(response => {
            if (response.ok) {
              //   
              navigate('/login')
            } else {
              alert('')
            }
          })
          .catch(error => {
            //   
            console.error('Error:', error)
          })
      }
    }
  }

  const onClickToLoginButton = () => {
    navigate('/api/login')
  }

  useEffect(() => {
    if (emailValid && pwValid && pwSameValid && nameValid) {
      setNotAllow(false)
      return
    }
    setNotAllow(true)
  }, [emailValid, pwValid, pwSameValid, nameValid])

  return (
    <>
      <S.Container>
        <S.InnerContainer>
          <Title text="" />
          <S.ContentWrap>
            <LabelInput
              type="text"
              placeholder=""
              value={email}
              onChange={handleEmail}
              width="100%"
            />
            {emailValid
              ? email.length > 0 && (
                  <S.OkMessageWrap>
                    <div>  .</div>
                  </S.OkMessageWrap>
                )
              : email.length > 0 && (
                  <S.ErrorMessageWrap>
                    <div>  .</div>
                  </S.ErrorMessageWrap>
                )}
            <LabelInput
              type="text"
              placeholder=""
              value={name}
              onChange={handleName}
              width="100%"
            />
            {name.length > 0 && name.length < 2 && (
              <S.ErrorMessageWrap>
                <div> 2  .</div>
              </S.ErrorMessageWrap>
            )}
            <LabelInput
              type="password"
              placeholder=""
              value={pw}
              onChange={handlePassword}
              width="100%"
            />
            {pwValid
              ? pw.length > 0 && (
                  <S.OkMessageWrap>
                    <div>  .</div>
                  </S.OkMessageWrap>
                )
              : pw.length > 0 && (
                  <S.ErrorMessageWrap>
                    <div>, ,   8  .</div>
                  </S.ErrorMessageWrap>
                )}
            <LabelInput
              type="password"
              placeholder=" "
              value={pwSame}
              onChange={handlePasswordSame}
              width="100%"
            />
            {pwSameValid
              ? pwSame.length > 0 && (
                  <S.OkMessageWrap>
                    <div> .</div>
                  </S.OkMessageWrap>
                )
              : pwSame.length > 0 && (
                  <S.ErrorMessageWrap>
                    <div>  .</div>
                  </S.ErrorMessageWrap>
                )}
          </S.ContentWrap>
          <div style={{ marginTop: '50px' }}>
            <S.SignUpButton onClick={onClickConfirmButton} disabled={notAllow}>
              
            </S.SignUpButton>
            <S.ToLoginButton onClick={onClickToLoginButton}>
               
            </S.ToLoginButton>
          </div>
        </S.InnerContainer>
      </S.Container>
    </>
  )
}

export default SignUp
